
body{
  margin: 0;
  padding: 0;
}
ul{
  list-style: none;
  margin: 0;
  padding: 0;
}
.div_clear{
  clear: both;
  height: 0;
}
.app{
  display: block;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: cover;
  -webkit-background-size: cover;
}

#app {
  min-width: 1000px;
}
.topnav{
  width: 100%;
  height: 32px;
  min-width: 1000px;
  border-bottom: 1px solid #ebebeb;
}
/* 改变背景图片时改变字体颜色 */
.topnav.changecolor span.topnav_left_changestyle,
.topnav.changecolor span.topnav_left_getmessage,
.topnav.changecolor .topnav_right_wrap>li>a{
  color: #FFF;
  text-decoration: underline;
}
.changecolor .topnav_left_div>span{
  color: #fff;
}
.topnav_left_wrap{
  float: left;
}
.topnav_left_wrap>div{
  float: left;
}
.topnav_left_changestyle_wrap,
.topnav_left_getmessage_wrap{
  display: inline-block;
  position: relative;
  margin:0 10px;
  height: 32px;
  line-height: 32px;
  color: #555;
  text-decoration: underline;
  outline: none;
  text-shadow: none;
  cursor: pointer;
}
.topnav_left_changestyle,
.topnav_left_getmessage{
  font-size: 12px;
}
/* css动画 */
/* 向下滑动显示，向上滑动消失 */
.dropshow-enter-active{
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out;
}
.dropshow-leave-active{
  -webkit-transition: all .5s ease-out;
  transition: all .5s ease-out;
}
.dropshow-enter,
.dropshow-leave-active{
  -webkit-transform: translateY(-300px);
          transform: translateY(-300px);
  opacity: 0;
}

.topnav_left_div{
  float: left;
  margin-left: 10px;
  line-height: 32px;
}
.topnav_left_div span{
  color: #555;
  font-size: 12px;
  text-decoration: none;
  cursor: pointer;
}
.topnav_left_div .topnav_text{
  color: #f03b32!important;
  font-weight: bold;
}
.topnav_left_div>img{
  position: relative;
  top: 5px;
}

.topnav_right_wrap{
	float: right;
}
.topnav_right_wrap>li{
	float: left;
}
.topnav_right_wrap>li>a{
	margin-left: 20px;
	line-height: 32px;
	text-decoration: underline;
	font-size: 13px;
	color: #555;
	font-weight: bold;
	cursor: pointer;
}
.topnav_right_wrap .more_products{
	margin-left: 10px;
	margin-right: 0px;
	width: 95px;
	text-align: center;
	background-color: #38f;
	cursor: default;
}
.topnav_right_wrap .more_products>a{
	margin-left: 0px;
	width: 60px;
	height: 23px;
	color: #fff;
	text-decoration: none;
	cursor: default;
}
.topnav_right_wrap .people_name>a,
.topnav_right_wrap .set_up>a{
	font-weight: normal;
}
.people_name,
.set_up{
	position: relative;
}
.topdropmenu{
	display: none;
}
/* 鼠标滑动事件 */
.people_name:hover .topdropmenu{
	display: block;
}
.set_up:hover .topdropmenu{
	display: block;
}
.right_more_products_wrap{
	display: block;
	position: absolute;
	right: 0;
	top: 0;
	width: 95px;
	height: 100%;
	border-left: 1px solid #e7e7e7;
	background-color: #f9f9f9;
}
.right_more_products_title{
    padding-bottom: 10px;
    margin: 0 auto;
    width: 66px;
    height: 30px;
    line-height: 34px;
    font-size: 13px;
    text-align: center;
	color: #333;
    border-bottom: #f0f0f0 1px solid;
    cursor: default;
}
.right_more_products_app>a{
	display: block;
    margin: 0 auto;
    width: 66px;
    height: 76px;
    font-size: 12px;
    text-align: center;
    line-height: 14px;
    border-bottom: #f0f0f0 1px solid;
    text-decoration: none;
    overflow: hidden;
    text-shadow: none;
    cursor: pointer;
}
/* 鼠标滑过 */
.right_more_products_app>a:hover>span{
	text-decoration: underline;
}
.right_more_products_app>a>img{
	display: block;
	width: auto;
	height: 36px;
	margin: 10px auto 5px;
	cursor: pointer;
}
.right_more_products_app>a>span{
	display: block;
	color: #666!important;
	text-decoration: none;
}
.right_more_products_app_more{
	margin-top: 15px;
	height: 30px;
}
.right_more_products_app_more>a{
	display: block;
    margin: 0 auto;
    width: 66px;
    height: 15px;
    font-size: 12px;
    text-align: center;
    line-height: 14px;
    color: #666;
    text-decoration: none;
    overflow: hidden;
    text-shadow: none;
    cursor: pointer;
}
/* 鼠标滑过 */
.right_more_products_app_more>a:hover{
	text-decoration: underline;
}

